import Topbar from '../../components/topBar/topbarx.vue'
export default {
	data() {
		return {
			title: '新增常用旅客',
			name: '蒋治坤',
			phone: '17683059017',
			id_card: '513922199702111997',
			card_end_time: '选择日期',
			isReading: true, // 防抖
		}
	},
	onLoad() {

	},
	onShow() {
		this.isReading = true
	},
	onUnload(){
		uni.hideLoading()
	},
	methods: {
		
		bindDateChange(e){
			console.log(e)
			this.card_end_time = e.target.value
		},
		 getDate(type) {
			const date = new Date();
			let year = date.getFullYear();
			let month = date.getMonth() + 1;
			let day = date.getDate();

			if (type === 'start') {
				year = year - 60;
			} else if (type === 'end') {
				year = year + 2;
			}
			month = month > 9 ? month : '0' + month;;
			day = day > 9 ? day : '0' + day;
			return `${year}-${month}-${day}`;
		},
		// 提交
		subClick(){
			let _this = this
			let userinfo = this.$store.state.userInfo
			if(this.name && this.phone && this.id_card && this.card_end_time){
				// 满足条件
				if(this.isReading){
					this.isReading =false
					uni.request({
						url: this.$http + '/api/user/addPassenger',
						method: 'POST',
						data: {
							token: userinfo.token,
							name: _this.name,
							phone: _this.phone,
							id_card: _this.id_card,
							card_end_time: _this.card_end_time,
							// user_id: userinfo.user_id
						},
						success(res) {
							console.log('添加常用旅客返回数据', res)
							if(res.data.code == 1){
								_this.isReading = true
								uni.showModal({
									title: '提示',
									content: '添加成功'
								})
								uni.navigateBack({
									
								})
							}else{
								_this.isReading = true
								uni.showModal({
									title: '提示',
									content: res.data.msg
								})
							}
						}
					})
				}
				
			}else{
				uni.showModal({
					title: '提示',
					content: '信息未填全'
				})
			}
		}
	},
	computed: {
		startDate() {
			return this.getDate('start');
		},
		endDate() {
			return this.getDate('end');
		}
	},
	components: {
		Topbar,
	}
}
